<template>
  <form @submit="addPost">
    <input type="text" name="title" v-model="title" placeholder="It's 12 pm..." class="block">
    <textarea name="content" v-model="content" class="block content">
      What a wonderful day!
    </textarea>
    <input type="submit" value="Post!">
  </form>
</template>

<script>
export default {
  name: "AddPost",
  methods: {
    addPost(e) {
      e.preventDefault();

      const newPost = {
        title: this.title,
        categories: null, // TODO
        content: this.content
      };

      this.$emit("add-post", newPost);
    }
  },
  data() {
    return {
      title: "",
      content: ""
    };
  }
};
</script>


<style scoped>
.block {
  display: block;
  margin: 10px auto;
}

.content {
  resize: none;
  overflow: hidden;
  min-height: 200px;
  width: 70%;
}
</style>
